@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,400,500,700);
@import url(https://fonts.googleapis.com/css?family=Raleway:300,400,500);
.btn {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 120px;
    height: 38px;
    font-size: 13px;
    font-weight: 500;
    line-height: 26px;
    text-transform: uppercase;
    background: transparent;
    border: 1px solid var(--card-color);
    outline: none;
    cursor: pointer;
    transition: all 0.24s ease;
}

.btn--basic {
    color: #ed1111;
}

.btn--basic:hover {
    background-color: #ed8e8e;
}

.btn--view {
    font-size: 12px;
    font-weight: 300;
    color: #999e9f;
    background-color: #d9d9d9;
}
.card {
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    height: 425px;
    width: 270px;
    margin: 20px 15px 30px;
    padding: 246px 10px 10px;
    border: 1px solid var(--card-color);
    box-shadow: none;
    box-sizing: border-box;
    transition: opacity 0.24s ease;
    -webkit-animation: opacity-animation 2s alternate;
            animation: opacity-animation 2s alternate;
}

.card:hover {
    box-shadow: 0 3px 7px rgba(0, 0, 0, 0.35);
}

.card__img {
    position: absolute;
    top: -20px;
    left: 10px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    width: 250px;
    height: 250px;
    border: 1px solid var(--card-color);
    background: #fff;
}

.card__type {
    position: absolute;
    top: 115px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 70px;
    height: 38px;
    color: #fff;
    font-size: 13px;
    font-weight: 500;
    line-height: 26px;
    text-transform: uppercase;
}

.card__sale {
    left: 0;
    background-color: #ed1111;
}

.card__new {
    right: 0;
    background-color: #000;
}

.card__title {
    font-size: 14px;
    line-height: 18px;
    text-align: left;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.card__description {
    margin-top: 10px;
    font-size: 11px;
    font-weight: 300;
    line-height: 15px;
    text-align: left;
}

.card__price {
    display: -webkit-flex;
    display: flex;
    margin-top: 14px;
}

.card__costDis {
    margin-right: 20px;
    color: #333;
    font-size: 25px;
    font-weight: 400;
    line-height: 26px;
    text-transform: uppercase;
}

.card__cost {
    color: #ed1111;
    font-size: 17px;
    font-weight: 400;
    line-height: 26px;
    text-transform: uppercase;
    text-decoration: line-through;
}

.card__control {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 17px;
}

@-webkit-keyframes opacity-animation {
    from { opacity: 0}
    to  {opacity: 1}
}

@keyframes opacity-animation {
    from { opacity: 0}
    to  {opacity: 1}
}
.footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 0 15px;
    margin-bottom: 50px;
}

.footer-block {
    width: 270px;
    padding: 40px 25px 55px;
    margin: 0 15px 20px;
    border: 1px solid #e5e5e5;
    box-sizing: border-box;
}

.footer-block__title {
    color: #333;
    font-family: Raleway;
    font-size: 20px;
    font-weight: 500;
    line-height: 58px;
    text-transform: uppercase;
    text-align: left;
}

.footer-block__text {
    height: 45px;
    color: #a5a4a4;
    font-size: 11px;
    font-weight: 300;
    line-height: 15px;
    text-align: left;
    overflow: hidden;
}

.footer-block__list {
    padding-left: 0;
}

.footer-block__item {
    position: relative;
    padding-left: 15px;
    color: #a5a4a4;
    font-size: 11px;
    font-weight: 300;
    line-height: 25px;
    list-style-type: none;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.footer-block__item::before {
    position: absolute;
    content: '';
    top: 6px;
    left: 0;
    width: 10px;
    height: 10px;
    background: url(../../static/media/item-right.cc566fca.svg) center center no-repeat;
}

.footer-block__item--location,
.footer-block__item--phone,
.footer-block__item--email,
.footer-block__item--skype {
    padding-left: 35px;
    min-height: 40px;
    text-align: left;
    white-space: normal;

}

.footer-block__item--email {
    line-height: 20px;
    margin-bottom: 10px;
}

.footer-block__item--location::before,
.footer-block__item--phone::before,
.footer-block__item--email::before,
.footer-block__item--skype::before {
    top: -3px;
    width: 30px;
    height: 30px;
}

.footer-block__item--location::before {
    background: url(../../static/media/map.e538d135.svg) left center no-repeat;
}

.footer-block__item--phone::before {
    background: url(../../static/media/phone.6c391143.svg) left center no-repeat;
}

.footer-block__item--email::before {
    background: url(../../static/media/email.d93a78d6.svg) left center no-repeat;
}

.footer-block__item--skype::before {
    background: url(../../static/media/skype.62a49ae8.svg) left center no-repeat;
}

.footer-block__email {
    color: #a3a6a6;
    text-decoration: none;
}

@media only screen and (min-width: 450px) {
    .footer-block {
        width: 370px;
    }
}
.market {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    padding: 30px 15px;
}

.load-more {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: 70px;
}
